:host {
  display: block;
  color: var(--pfe-broadcasted--color--text);
}

:host [tabindex] {
  display: flex;
}

:host .container {
  margin: 0;
  width: 100%;
  padding: var(--pfe-tabs__panel--Padding, var(--pfe-theme--container-spacer, 16px) 0);
  border-top: var(--pfe-tabs__panel--BorderTop, 0);
  border-right: var(--pfe-tabs__panel--BorderRight, 0);
  border-bottom: var(--pfe-tabs__panel--BorderBottom, 0);
  border-left: var(--pfe-tabs__panel--BorderLeft, 0);
}

:host([pfe-variant="earth"]) {
  --pfe-tabs__panel--Padding: calc(var(--pfe-theme--container-spacer, 16px) * 2);
  --pfe-tabs__panel--BorderRight: var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
  --pfe-tabs__panel--BorderBottom: var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
  --pfe-tabs__panel--BorderLeft: var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
}

@media screen and (min-width: 768px) {
  :host([pfe-variant="wind"][vertical]) {
    --pfe-tabs__panel--Padding: var(--pfe-theme--container-spacer, 16px) 0 var(--pfe-theme--container-spacer, 16px) calc(var(--pfe-theme--container-spacer, 16px) * 2);
  }
  :host([pfe-variant="earth"][vertical]) {
    --pfe-tabs__panel--BorderTop:   var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
  }
}

:host([on="dark"]) {
  --pfe-broadcasted--color--text:               var(--pfe-theme--color--text--on-dark, #fff);
  --pfe-broadcasted--color--ui-link:            var(--pfe-theme--color--ui-link--on-dark, #99ccff);
  --pfe-broadcasted--color--ui-link--visited:   var(--pfe-theme--color--ui-link--on-dark--visited, #b38cd9);
  --pfe-broadcasted--color--ui-link--hover:     var(--pfe-theme--color--ui-link--on-dark--hover, #cce6ff);
  --pfe-broadcasted--color--ui-link--focus:     var(--pfe-theme--color--ui-link--on-dark--focus, #cce6ff);
  color: pfe-colors(text--on-dark);
}

:host([on="light"]) {
  --pfe-broadcasted--color--text:               var(--pfe-theme--color--text, #333);
  --pfe-broadcasted--color--ui-link:            var(--pfe-theme--color--ui-link, #06c);
  --pfe-broadcasted--color--ui-link--visited:   var(--pfe-theme--color--ui-link--visited, rebeccapurple);
  --pfe-broadcasted--color--ui-link--hover:     var(--pfe-theme--color--ui-link--hover, #003366);
  --pfe-broadcasted--color--ui-link--focus:     var(--pfe-theme--color--ui-link--focus, #003366);
  color: pfe-colors(text);
}

:host([hidden]) {
  display: none;
}
